import {
  Component, EventEmitter,
  HostListener, Input,
  Output, TemplateRef, ViewChild
} from '@angular/core';
import { IManagedObject, InventoryService } from '@c8y/client';
import {
  ActionControl, AlertService, GainsightService, gettext, HeaderActionControl, Pagination, Row
} from '@c8y/ngx-components';
import { SubAssetsService } from '../sub-assets.service';

@Component({
  selector: 'c8y-assign-devices',
  templateUrl: './assign-devices.component.html'
})
export class AssignDevicesComponent {
  @Input() currentGroupId: string;
  @Input() refresh = new EventEmitter<any>();
  @Output() onCancel = new EventEmitter<any>();
  @Output() onShowChildDevices = new EventEmitter<boolean>();
  @Output() selectedDevice = new EventEmitter<IManagedObject>();
  @ViewChild('showDevicesToggle', { read: TemplateRef }) showDevicesToggle: TemplateRef<any>;

  deviceQueryStringOutput: string;
  pendingStatus: boolean = false;
  pagination: Pagination = { pageSize: 20, currentPage: 1 };
  selected: string[] = [];
  baseQuery: any;
  canAssignDevice: boolean = false;
  actionControls: ActionControl[] = [];
  headerActionControls: HeaderActionControl[] = [];
  showChildren: boolean = false;
  readonly isSelectable = true;

  constructor(
    private alert: AlertService,
    private subAssetsService: SubAssetsService,
    private inventoryService: InventoryService,
    private gainsightService: GainsightService
  ) { }

  @HostListener('document:keydown.enter', ['$event']) onEnterKeyDown(event: KeyboardEvent) {
    if (this.selected.length > 0) {
      this.assignDevices();
    }
  }

  @HostListener('document:keydown.escape', ['$event']) onEscapeKeyDown(event: KeyboardEvent) {
    this.onCancel.emit();
  }

  async ngOnInit() {
    this.setNotIncludedInGroupQuery();
    this.canAssignDevice = await this.subAssetsService.canAssignDevice({
      id: this.currentGroupId
    } as IManagedObject);
    this.setHeaderActionControls();
  }

  setNotIncludedInGroupQuery() {
    const notIncludedInGroupQuery = { __not: { __bygroupid: this.currentGroupId } };
    this.baseQuery = notIncludedInGroupQuery;
  }

  setHeaderActionControls() {
    const headerActionControls: HeaderActionControl[] = [];
    const showChildDevices = {
      type: 'DISPLAY_CHILD_DEVICES_BUTTON',
      text: gettext('Display child devices button'),
      template: this.showDevicesToggle,
      callback: () => {
        this.showChildren = !this.showChildren;
        this.setActionControls(this.showChildren);
        this.gainsightService.triggerEvent('assignDevices:DisplayChildDevices');
      }
    };
    headerActionControls.push(showChildDevices);
    this.headerActionControls = headerActionControls;
  }

  setActionControls(showChildren: boolean) {
    const actionControls: ActionControl[] = [];

    const selectChildrenAction: ActionControl = {
      type: 'SHOW_TARGET_CHILD_DEVICES',
      icon: 'enter-bottom',
      text: gettext('Show target child devices'),
      callback: (asset: Row) => this.selectChildren(asset as IManagedObject),
      showIf: (asset: Row) => asset.childDevices.references.length > 0
    };

    if (showChildren) {
      actionControls.push(selectChildrenAction);
    }

    this.actionControls = actionControls;
    this.refresh.emit();
  }

  async assignDevices() {
    if (this.canAssignDevice === false) {
      return;
    }
    this.pendingStatus = true;

    try {
      await this.inventoryService.childAssetsBulkAdd(this.selected, this.currentGroupId);
      this.refresh.emit();
      this.alert.success(gettext('Devices assigned to the group.'));
    } catch (error) {
      this.alert.danger(gettext('Could not assign devices to the group'), error);
    }
    this.pendingStatus = false;
    this.selected = [];
    this.onCancel.emit();
  }

  onSelected(selectedDevicesIDs: string[]) {
    this.selected = selectedDevicesIDs;
  }

  selectChildren(asset: IManagedObject) {
    this.onShowChildDevices.emit(true);
    this.selectedDevice.emit(asset);
  }
}
