import { Injectable, Optional } from '@angular/core';
import {
  IManagedObject,
  InventoryService,
  IRule,
  QueriesUtil,
  SmartGroupsService,
  SmartRulesService,
  UserService
} from '@c8y/client';
import {
  ActionControl,
  AlertService,
  AppStateService,
  AssetTypesService,
  BulkActionControl,
  GainsightService,
  gettext,
  ModalService,
  Pagination,
  Permissions,
  UserPreferencesService
} from '@c8y/ngx-components';
import { AssetNodeService } from '@c8y/ngx-components/assets-navigator';
import {
  AlarmsDeviceGridColumn,
  DeviceGridColumn,
  DeviceGridService,
  ImeiDeviceGridColumn,
  ModelDeviceGridColumn,
  NameDeviceGridColumn,
  RegistrationDateDeviceGridColumn,
  SerialNumberDeviceGridColumn,
  SystemIdDeviceGridColumn
} from '@c8y/ngx-components/device-grid';
import { TranslateService } from '@ngx-translate/core';
import { AssetTypeGridColumn } from './columns/asset-type-grid-column';

@Injectable({
  providedIn: 'root'
})
export class SubAssetsService extends DeviceGridService {
  queriesUtil: QueriesUtil;
  protected GRID_CONFIG_DEFAULT_STORAGE_KEY = 'sub-assets-grid-config';
  private IS_DEVICE_GROUP_FRAGMENT = 'c8y_IsDeviceGroup';
  private IS_DYNAMIC_GROUP_FRAGMENT = 'c8y_IsDynamicGroup';

  constructor(
    protected translateService: TranslateService,
    protected inventoryService: InventoryService,
    protected userService: UserService,
    protected appState: AppStateService,
    protected user: UserService,
    protected assetNodeService: AssetNodeService,
    protected smartGroupsService: SmartGroupsService,
    protected smartRulesService: SmartRulesService,
    protected alertService: AlertService,
    protected permissionsService: Permissions,
    protected modal: ModalService,
    protected assetTypes: AssetTypesService,
    protected userPreferencesService: UserPreferencesService,
    @Optional() protected gainsightService?: GainsightService
  ) {
    super(
      inventoryService,
      userService,
      translateService,
      alertService,
      modal,
      undefined,
      userPreferencesService,
      gainsightService
    );
  }

  async getCustomProperties(group: IManagedObject): Promise<IManagedObject[]> {
    const assetType = this.assetTypes.getAssetTypeByName(group.type);
    if (assetType) {
      const { data } = await this.inventoryService.childAdditionsList(assetType, {
        pageSize: 2000,
        fragmentType: 'c8y_IsAssetProperty'
      });
      return data;
    }
    return [];
  }

  getDefaultColumns(filterable: boolean = true, sortable: boolean = true): DeviceGridColumn[] {
    const defaultColumns = [
      new AssetTypeGridColumn({ sortOrder: 'desc' }),
      new NameDeviceGridColumn({ sortOrder: 'asc' }),
      new ModelDeviceGridColumn(),
      new SerialNumberDeviceGridColumn({ visible: false }),
      new RegistrationDateDeviceGridColumn({ visible: false }),
      new SystemIdDeviceGridColumn({ visible: false }),
      new ImeiDeviceGridColumn({ visible: false }),
      new AlarmsDeviceGridColumn()
    ];
    return defaultColumns;
  }

  getDefaultPagination(): Pagination {
    const { pagination } = this.getConfig();
    return {
      pageSize: pagination.pageSize,
      currentPage: 1
    };
  }

  getDefaultActionControls(): ActionControl[] {
    return [];
  }

  async unassignAsset(asset: IManagedObject, parentRef: IManagedObject) {
    const { id: assetId } = asset;
    const { id: parentId } = parentRef;

    if (this.isDevice(asset)) {
      try {
        await this.inventoryService.childAssetsRemove(assetId, parentId);
        const alertMessage = this.translateService.instant(gettext('Asset unassigned.'));
        this.alertService.success(alertMessage);
      } catch (error) {
        const alertMessage = this.translateService.instant(gettext('Could not unassign asset.'));
        this.alertService.danger(alertMessage);
      }
      await this.deactivateSmartrulesForAsset(asset, parentRef);
    }
  }

  isDevice(asset: IManagedObject): boolean {
    return (
      !asset.hasOwnProperty(this.IS_DEVICE_GROUP_FRAGMENT) &&
      !asset.hasOwnProperty(this.IS_DYNAMIC_GROUP_FRAGMENT)
    );
  }

  async deleteAsset(asset: IManagedObject, parentRef: IManagedObject, params = {}) {
    const isGroup =
      asset.hasOwnProperty(this.IS_DEVICE_GROUP_FRAGMENT) ||
      this.smartGroupsService.isSmartGroup(asset);

    if (isGroup) {
      await this.deleteGroup(asset, params);
    } else {
      await this.deleteDevice(asset, params);
    }

    if (
      parentRef &&
      !this.smartGroupsService.isSmartGroup(asset) &&
      !this.smartGroupsService.isSmartGroupV2(asset)
    ) {
      await this.deactivateSmartrulesForAsset(asset, parentRef);
    }
  }

  shouldShowWithDeviceUserCheckbox(asset: IManagedObject): boolean {
    const { owner, c8y_IsDevice: isRootDevice } = asset;
    const hasDeviceUserAsOwner = asset.owner && this.isDeviceUser(owner);

    return Boolean(isRootDevice && hasDeviceUserAsOwner);
  }

  getDefaultBulkActionControls(): BulkActionControl[] {
    return [];
  }

  async getData(
    columns: DeviceGridColumn[],
    pagination: Pagination,
    parentReference: IManagedObject,
    baseQuery: any = {}
  ) {
    const isRoot = !parentReference;
    if (isRoot) {
      const query = this.buildCombinedRootQueryFilter(columns, pagination);
      return this.assetNodeService.getRootNodes({ ...pagination, query });
    }
    const filters = {
      ...this.getAssetsFilters(columns, pagination, baseQuery),
      withParents: false
    };
    if (this.assetNodeService.isGroup(parentReference)) {
      return this.assetNodeService.getGroupItems(parentReference.id, filters);
    }
    if (this.assetNodeService.isDynamicGroup(parentReference)) {
      return this.assetNodeService.getDynamicGroupItems(
        parentReference.c8y_DeviceQueryString,
        filters
      );
    }
    if (this.assetNodeService.isDevice(parentReference)) {
      return this.assetNodeService.getDeviceChildren(parentReference.id, filters);
    }
  }

  async getCount(
    columns: DeviceGridColumn[],
    pagination: Pagination,
    parentReference: IManagedObject,
    baseQuery: any = {}
  ): Promise<number> {
    const defaultFilters = {
      pageSize: 1,
      withChildren: false
    };
    const filters = !parentReference
      ? {
          query: this.buildCombinedRootQueryFilter(columns, pagination),
          ...defaultFilters
        }
      : {
          ...this.getAssetsFilters(columns, pagination, baseQuery),
          ...defaultFilters
        };
    return this.getAssetsStatistics(parentReference, filters);
  }

  getTotal(parentReference: IManagedObject, baseQuery: any = {}): Promise<number> {
    const queryFilter = this.assetNodeService.rootQueryFilter();
    const query = !parentReference
      ? this.queriesUtil.addAndFilter(queryFilter, baseQuery)
      : baseQuery;
    const filters = {
      query: this.queriesUtil.buildQuery(query),
      withChildren: false,
      withTotalPages: true,
      pageSize: 1
    };
    return this.getAssetsStatistics(parentReference, filters);
  }

  async canEditGroup(group: IManagedObject): Promise<boolean> {
    return await this.permissionsService.canEdit(['ROLE_INVENTORY_ADMIN'], group);
  }

  canCreateGroup(): boolean {
    const currentUser = this.appState.currentUser.value;
    const hasAdminRole = this.user.hasAnyRole(currentUser, [
      'ROLE_INVENTORY_ADMIN',
      'ROLE_INVENTORY_CREATE'
    ]);
    return hasAdminRole;
  }

  async canAssignDevice(group: IManagedObject): Promise<boolean> {
    return await this.permissionsService.canEdit(['ROLE_INVENTORY_ADMIN'], group);
  }

  canEditSmartGroup(): boolean {
    const SMART_GROUPS_ROLES_EDIT = ['ROLE_SMARTGROUP_UPDATE', 'ROLE_SMARTGROUP_ADMIN'];
    return this.permissionsService.hasAnyRole(SMART_GROUPS_ROLES_EDIT);
  }

  canDeleteSmartGroup(): boolean {
    const SMART_GROUPS_ROLES_DELETE = ['ROLE_SMARTGROUP_ADMIN', 'ROLE_INVENTORY_ADMIN'];
    return this.permissionsService.hasAnyRole(SMART_GROUPS_ROLES_DELETE);
  }

  isSmartGroup(group: IManagedObject): boolean {
    return (
      this.smartGroupsService.isSmartGroup(group) || this.smartGroupsService.isSmartGroupV2(group)
    );
  }

  isUsingInventoryRoles() {
    const currentUser = this.appState.currentUser.value;
    const hasAnyInventoryRole = this.user.hasAnyRole(currentUser, [
      'ROLE_INVENTORY_ADMIN',
      'ROLE_INVENTORY_READ',
      'ROLE_INVENTORY_CREATE'
    ]);
    return !hasAnyInventoryRole;
  }

  protected async getAssetsStatistics(
    parentReference: IManagedObject,
    filters: object
  ): Promise<number> {
    const isRoot = !parentReference;
    if (isRoot) {
      return (await this.assetNodeService.getRootNodes(filters)).paging.totalPages;
    }
    if (this.assetNodeService.isGroup(parentReference)) {
      return (await this.assetNodeService.getGroupItems(parentReference.id, filters)).paging
        .totalPages;
    }
    if (this.assetNodeService.isDynamicGroup(parentReference)) {
      return (
        await this.assetNodeService.getDynamicGroupItems(
          parentReference.c8y_DeviceQueryString,
          filters
        )
      ).paging.totalPages;
    }
    if (this.assetNodeService.isDevice(parentReference)) {
      return (await this.assetNodeService.getDeviceChildren(parentReference.id, filters)).paging
        .totalPages;
    }
  }

  protected buildCombinedRootQueryFilter(columns, pagination) {
    const queryFilter = this.assetNodeService.rootQueryFilter();
    const userQuery = this.getQueryObj(columns, pagination);
    const queryPart = this.queriesUtil.addOrderbys(queryFilter, userQuery.__orderby, 'append');
    const fullQuery = this.queriesUtil.addAndFilter(queryPart, userQuery.__filter);
    return this.queriesUtil.buildQuery(fullQuery);
  }

  private async deleteGroup(group: IManagedObject, params: any = {}) {
    const { cascade } = params;

    try {
      this.smartGroupsService.isSmartGroup(group) || this.smartGroupsService.isSmartGroupV2(group)
        ? await this.smartGroupsService.delete(group, { cascade })
        : await this.inventoryService.delete(group, { cascade });

      const alertMessage = this.translateService.instant(gettext('Asset deleted.'));
      this.alertService.success(alertMessage);
    } catch (error) {
      const alertMessage = this.translateService.instant(gettext('Could not delete asset.'));
      this.alertService.danger(alertMessage);
    }
  }

  private async deleteDevice(device: IManagedObject, params: any = {}) {
    const { cascade, withDeviceUser } = params;
    try {
      const { owner } = device;
      const shouldRemoveOwner = withDeviceUser && owner && this.isDeviceUser(owner);

      shouldRemoveOwner
        ? await this.deleteDeviceWithUser(device, cascade)
        : await this.inventoryService.delete(device, { cascade });

      const alertMessage = this.translateService.instant(gettext('Asset deleted.'));
      this.alertService.success(alertMessage);
    } catch (error) {
      const alertMessage = this.translateService.instant(gettext('Could not delete asset.'));
      this.alertService.danger(alertMessage);
    }
  }

  private async deactivateSmartrulesForAsset(asset: IManagedObject, parentRef: IManagedObject) {
    const { id: assetId } = asset;
    const { id: parentId } = parentRef;
    const rules: IRule[] = (await this.smartRulesService.listByContext(parentId)).data;

    const upateSmartrulesPromises = rules.map(rule =>
      this.smartRulesService.bulkDeactivateEnabledSources(rule, [assetId])
    );

    try {
      await Promise.all(upateSmartrulesPromises);
    } catch (error) {
      const alertMessage = this.translateService.instant(
        gettext('Could not deactivate smart rules.')
      );
      this.alertService.danger(alertMessage);
    }
  }

  private isDeviceUser(userId: string) {
    return userId.match(/^device_/);
  }

  private async deleteDeviceWithUser(device: IManagedObject, cascade: boolean) {
    const params = { cascade, withDeviceUser: true };
    try {
      return await this.inventoryService.delete(device, params);
    } catch (error) {
      return await this.inventoryService.delete(device, { cascade });
    }
  }

  private getAssetsFilters(columns: DeviceGridColumn[], pagination: Pagination, baseQuery) {
    const query = this.queriesUtil.addAndFilter(this.getQueryObj(columns), baseQuery);
    return {
      query: this.queriesUtil.buildQuery(query),
      pageSize: pagination.pageSize || this.DEFAULT_PAGE_SIZE,
      currentPage: pagination.currentPage,
      withTotalPages: true
    };
  }
}
