import { Component, EventEmitter, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IIdentified, IManagedObject } from '@c8y/client';
import { DisplayOptions } from '@c8y/ngx-components';
import { Subscription } from 'rxjs';
import { SubAssetsService } from './sub-assets.service';
import { AssetNodeService } from '@c8y/ngx-components/assets-navigator';

@Component({
  selector: 'c8y-sub-assets',
  templateUrl: './sub-assets.component.html'
})
export class SubAssetsComponent implements OnInit, OnDestroy {
  currentGroupId: IIdentified;
  showChildrenForDevice: IManagedObject;
  title: string;
  toggle: boolean = false;
  showAddGroup: boolean = false;
  showAssignDevices: boolean = false;
  requestInProgress: boolean;
  group: IManagedObject;
  showAssignChildDevices: boolean = false;
  refresh: EventEmitter<any> = new EventEmitter();
  filterable: boolean = true;
  shouldDisableAddGroup: boolean = false;
  shouldDisableAssignDevices: boolean = false;
  shouldShowAssetsProperties: boolean = false;
  displayOptions: DisplayOptions;
  customProperties: IManagedObject[] = [];
  private dataSub: Subscription;

  constructor(
    private route: ActivatedRoute,
    private subAssetsService: SubAssetsService,
    private assetNodeService: AssetNodeService
  ) {
    this.dataSub = this.route.parent.data.subscribe(({ contextData }) => {
      this.init(contextData);
    });
  }

  async ngOnInit() {
    this.displayOptions = {
      striped: true,
      bordered: false,
      gridHeader: true,
      filter: !this.isSmartGroup()
    };
  }

  async init(contextData) {
    this.title = contextData.name;
    this.currentGroupId = contextData.id;
    this.group = contextData;
    this.filterable = !this.assetNodeService.isDynamicGroup(contextData);
    this.shouldDisableAddGroup = !this.subAssetsService.canCreateGroup();
    this.shouldDisableAssignDevices = !(await this.subAssetsService.canAssignDevice(this.group));
    this.customProperties = await this.subAssetsService.getCustomProperties(this.group);
    this.shouldShowAssetsProperties = this.customProperties.length > 0;
  }

  groupChange(group: IManagedObject): void {
    /**
     * Group is cached in the context-route guard. To avoid loading old data (when switching tabs) we need to update
     * this object in the cache.
     */
    Object.assign(this.group, group);
    this.title = group.name;
    this.refresh.emit();
  }

  isSmartGroup() {
    return this.subAssetsService.isSmartGroup(this.group);
  }

  ngOnDestroy(): void {
    if (this.dataSub) {
      this.dataSub.unsubscribe();
    }
  }
}
