import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { IManagedObject, InventoryService, SmartGroupsService } from '@c8y/client';
import { AlertService, gettext, ModalService, Status } from '@c8y/ngx-components';
import { AssetNodeService } from '@c8y/ngx-components/assets-navigator';
import { TranslateService } from '@ngx-translate/core';
import { SubAssetsService } from './sub-assets.service';

@Component({
  selector: 'c8y-group-info',
  templateUrl: './group-info.component.html'
})
export class GroupInfoComponent implements OnChanges {
  @Input() group: IManagedObject;
  @Output() onGroupChange = new EventEmitter();
  canEdit: boolean;
  groupIcon: string;
  smartGroupFilter: string;
  filterHintMsg: string;
  label: string;

  private filterMsg = gettext(
    'Smart groups are groups dynamically constructed based on filtering criteria.'
  );
  private canEditMsg = gettext('You can edit the filter here.');
  private readonly GROUP_UPDATED_MSG = gettext('Group updated.');

  constructor(
    private inventory: InventoryService,
    private subAssetsService: SubAssetsService,
    private smartGroupsService: SmartGroupsService,
    private alertService: AlertService,
    private translate: TranslateService,
    private modalService: ModalService,
    private assetNodeService: AssetNodeService
  ) {}

  async ngOnChanges(changes: SimpleChanges) {
    if (changes.group) {
      this.canEdit = this.smartGroupsService.isSmartGroupV2(this.group)
        ? this.subAssetsService.canEditSmartGroup()
        : await this.subAssetsService.canEditGroup(this.group);
      this.groupIcon = this.assetNodeService.icon(this.group);
      this.smartGroupFilter = this.group.c8y_DeviceQueryString;
      this.label = this.assetNodeService.isAsset(this.group) ? this.group.type : gettext('Group');
      this.setHintMsg();
    }
  }

  isSmartGroup() {
    return this.subAssetsService.isSmartGroup(this.group);
  }

  async update(partialGroup: Partial<IManagedObject>) {
    try {
      const isSmartGroup: boolean = this.subAssetsService.isSmartGroup(this.group);
      isSmartGroup
        ? await this.updateSmartGroup(partialGroup)
        : await this.updateGroup(partialGroup);
    } catch (error) {
      this.alertService.addServerFailure(error);
    }
  }

  private setHintMsg() {
    const filterMsgTranslated = this.translate.instant(this.filterMsg);
    const canEditMsgTranslated = this.translate.instant(this.canEditMsg);
    this.filterHintMsg = this.canEdit
      ? `${filterMsgTranslated} ${canEditMsgTranslated}`
      : this.filterMsg;
  }

  private async updateGroup(partialGroup: Partial<IManagedObject>) {
    const { id } = this.group;
    const group: Partial<IManagedObject> = { id, ...partialGroup };

    const updatedGroup = (await this.inventory.update(group)).data;
    this.setGroup(updatedGroup);
  }

  private async updateSmartGroup(partialGroup: Partial<IManagedObject>) {
    const { id } = this.group;
    const { c8y_DeviceQueryString } = partialGroup;
    const group: Partial<IManagedObject> = { id, ...partialGroup };
    let updatedGroup: IManagedObject;

    if (!c8y_DeviceQueryString) {
      updatedGroup = (await this.smartGroupsService.update(group)).data;
      this.setGroup(updatedGroup);
      return;
    }

    try {
      const modalBody = gettext(
        'You are about to change the smart group filter. Do you want to proceed?'
      );
      const title = gettext('Smart group filter');
      await this.modalService.confirm(title, modalBody, Status.WARNING, {
        ok: gettext('Save'),
        cancel: gettext('Cancel')
      });

      if (!(await this.isQueryExecutable(c8y_DeviceQueryString))) {
        return;
      }
    } catch (e) {
      return;
    }
    updatedGroup = (await this.smartGroupsService.update(group)).data;
    this.setGroup(updatedGroup);
  }

  private setGroup(group: IManagedObject) {
    this.onGroupChange.emit(group);
    this.alertService.success(this.GROUP_UPDATED_MSG);
  }

  private async isQueryExecutable(query: string): Promise<boolean> {
    try {
      const filter = { q: query };
      await this.inventory.list(filter);
      return true;
    } catch (error) {
      this.alertService.addServerFailure(error);
      return false;
    }
  }
}
