import { Component, Input, Output, EventEmitter, HostListener } from '@angular/core';
import { AlertService, gettext } from '@c8y/ngx-components';
import { InventoryService } from '@c8y/client';
import { SubAssetsService } from '../sub-assets.service';
import { IManagedObject } from '@c8y/client';

@Component({
  selector: 'c8y-assign-child-devices',
  templateUrl: './assign-child-devices.component.html'
})
export class AssignChildDevicesComponent {
  @Input() currentGroupId: string;
  @Input() parentDevice: IManagedObject;
  @Output() onCancel = new EventEmitter<any>();
  @Input() refresh = new EventEmitter<any>();

  selected: string[] = [];
  baseQuery: any;
  canAssignDevice: boolean = false;
  pendingStatus: boolean = false;

  constructor(
    private alert: AlertService,
    private subAssetsService: SubAssetsService,
    private inventoryService: InventoryService
  ) {}

  @HostListener('document:keydown.enter', ['$event']) onEnterKeyDown(event: KeyboardEvent) {
    if (this.selected.length > 0) {
      this.assignDevices();
    }
  }

  @HostListener('document:keydown.escape', ['$event']) onEscapeKeyDown(event: KeyboardEvent) {
    this.onCancel.emit();
  }

  async ngOnInit() {
    this.setNotIncludedInGroupQuery();
    this.canAssignDevice = await this.subAssetsService.canAssignDevice({
      id: this.currentGroupId
    } as IManagedObject);
  }

  setNotIncludedInGroupQuery() {
    const notIncludedInGroupQuery = { __not: { __bygroupid: this.currentGroupId } };
    this.baseQuery = notIncludedInGroupQuery;
  }

  async assignDevices() {
    if (this.canAssignDevice === false) {
      return;
    }
    this.pendingStatus = true;

    try {
      await this.inventoryService.childAssetsBulkAdd(this.selected, this.currentGroupId);
      this.refresh.emit();
      this.alert.success(gettext('Child devices assigned to the group.'));
    } catch (error) {
      this.alert.danger(gettext('Could not assign child devices to the group'), error);
    }
    this.pendingStatus = false;
    this.selected = [];
    this.onCancel.emit();
  }

  onSelected(selectedDevicesIDs: string[]) {
    this.selected = selectedDevicesIDs;
  }
}
