import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { IManagedObject, SmartGroupsService } from '@c8y/client';
import {
  ActionControl,
  BuiltInActionType,
  BulkActionControl,
  DataGridComponent,
  DataSourceModifier,
  DisplayOptions,
  gettext,
  Pagination,
  Row,
  ServerSideDataResult
} from '@c8y/ngx-components';
import { DeviceGridColumn } from '@c8y/ngx-components/device-grid';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DeleteModalCheckboxes } from './delete-assets-modal';
import { DeleteAssetsModalComponent } from './delete-assets-modal/delete-assets-modal.component';
import { SubAssetsService } from './sub-assets.service';
import { UnassignModalComponent } from './unassign-assets-modal/unassign-modal.component';
@Component({
  selector: 'c8y-sub-assets-grid',
  templateUrl: './sub-assets-grid.component.html'
})
export class SubAssetsGridComponent implements OnInit {
  @Input('parent-group') parentGroup: IManagedObject;
  @Input() refresh: EventEmitter<any>;
  @Input() title: string = gettext('Subassets');
  @Input() emptyStateText: string = gettext(
    'Add your first group or assign devices using the buttons on the action bar.'
  );
  @Input() loadingItemsLabel: string = gettext('Loading assets…');

  get columns() {
    return this._columns;
  }
  @Input() set columns(value: DeviceGridColumn[]) {
    if (value) {
      this._columns = this.subAssetsGridService.getUserConfiguredColumns(value);
    } else {
      this._columns = this.subAssetsGridService.getUserConfiguredColumns(
        this.subAssetsGridService.getDefaultColumns()
      );
    }
  }
  @Input('pagination') set _pagination(value: Pagination) {
    if (value) {
      this.pagination = value;
    } else {
      this.pagination = this.subAssetsGridService.getDefaultPagination();
    }
  }
  @Input('actionControls') set _actionControls(value: ActionControl[]) {
    if (value) {
      this.actionControls = value;
    } else {
      this.actionControls = this.subAssetsGridService.getDefaultActionControls();
    }
  }
  @Input() selectable: boolean = false;
  @Input() baseQuery: any = {};
  @Input('bulkActionControls') set _bulkActionControls(value: BulkActionControl[]) {
    if (value) {
      this.bulkActionControls = value;
    } else {
      this.bulkActionControls = this.subAssetsGridService.getDefaultBulkActionControls();
    }
  }
  @Input() filterable: boolean = true;
  @Input() sortable: boolean = true;
  @Output() onColumnsChange: EventEmitter<DeviceGridColumn[]> = new EventEmitter<
    DeviceGridColumn[]
  >();
  @Output() itemsSelect: EventEmitter<string[]> = new EventEmitter<string[]>();

  pagination: Pagination = this.subAssetsGridService.getDefaultPagination();
  showCounterWarning: boolean = false;
  actionControls: ActionControl[];
  bulkActionControls: BulkActionControl[] =
    this.subAssetsGridService.getDefaultBulkActionControls();
  serverSideDataCallback: any;

  @ViewChild(DataGridComponent, { static: true })
  dataGrid: DataGridComponent;

  displayOptions: DisplayOptions = {
    striped: true,
    bordered: false,
    gridHeader: true,
    filter: true
  };

  private _columns: DeviceGridColumn[];

  get isRootGroup() {
    return !this.parentGroup;
  }

  get getInfiniteScrollMode() {
    return this.isRootGroup && this.subAssetsGridService.isUsingInventoryRoles()
      ? 'auto'
      : undefined;
  }

  @Input('displayOptions')
  set _displayOptions(displayOptions) {
    this.displayOptions = { ...this.displayOptions, ...displayOptions };
  }

  constructor(
    public subAssetsGridService: SubAssetsService,
    private bsModalService: BsModalService,
    private smartGroupsService: SmartGroupsService
  ) {
    this.serverSideDataCallback = this.onDataSourceModifier.bind(this);
  }

  ngOnInit() {
    this.columns = this.subAssetsGridService.getDefaultColumns(this.filterable, this.sortable);
    if (!this.filterable || !this.sortable) {
      this.columns.forEach(column => {
        column.filterable = this.filterable;
        column.sortable = this.sortable;
      });
    }
    this.setActionControls();
  }

  async setActionControls() {
    const actionControls: ActionControl[] = [];
    const { data: isMicroserviceInstalled } =
      await this.smartGroupsService.isSmartGroupsV2MicroserviceInstalled();

    const deleteAction: ActionControl = {
      type: BuiltInActionType.Delete,
      callback: (asset: Row) => this.onDeleteAsset(asset as IManagedObject, this.parentGroup),
      showIf: (asset: Row) => {
        if (this.smartGroupsService.isSmartGroupV2(asset as IManagedObject)) {
          return isMicroserviceInstalled ? this.subAssetsGridService.canDeleteSmartGroup() : false;
        }

        if (this.smartGroupsService.isSmartGroup(asset as IManagedObject)) {
          return this.subAssetsGridService.canDeleteSmartGroup();
        }

        return true;
      }
    };

    actionControls.push(deleteAction);

    const unassignAction: ActionControl = {
      type: 'UNASSIGN',
      icon: 'unlink',
      text: gettext('Unassign'),
      callback: (asset: Row) => this.onUnassignAsset(asset as IManagedObject, this.parentGroup),
      showIf: (asset: Row) =>
        this.subAssetsGridService.isDevice(asset as IManagedObject) &&
        !this.subAssetsGridService.isSmartGroup(this.parentGroup as IManagedObject)
    };

    actionControls.push(unassignAction);

    if (!this.actionControls) {
      this.actionControls = actionControls;
    }
  }

  onUnassignAsset(asset: IManagedObject, parentRef: IManagedObject) {
    const initialState = {
      asset
    };
    const modalRef = this.bsModalService.show(UnassignModalComponent, { initialState });

    modalRef.content.closeSubject.subscribe(async (result: boolean) => {
      if (result) {
        await this.subAssetsGridService.unassignAsset(asset, parentRef);
        this.refresh.emit();
      }
    });
  }

  async onDeleteAsset(asset: IManagedObject, parentRef: IManagedObject) {
    const initialState = {
      showWithDeviceUserCheckbox: this.subAssetsGridService.shouldShowWithDeviceUserCheckbox(asset),
      asset,
      showWithCascadeCheckbox:
        !this.smartGroupsService.isSmartGroup(asset) &&
        !this.smartGroupsService.isSmartGroupV2(asset)
    };

    const modalRef = this.bsModalService.show(DeleteAssetsModalComponent, { initialState });

    modalRef.content.closeSubject.subscribe(async (result: DeleteModalCheckboxes) => {
      if (result) {
        await this.subAssetsGridService.deleteAsset(asset, parentRef, result);
        this.showCounterWarning = true;
        this.refresh.emit();
      }
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.parentGroup && !changes.parentGroup.firstChange) {
      this.dataGrid.reload();
    }
  }

  trackByName(_index, column: DeviceGridColumn): string {
    return column.name;
  }

  async onDataSourceModifier(
    dataSourceModifier: DataSourceModifier
  ): Promise<ServerSideDataResult> {
    const promises = [];
    let counters;

    promises.push(
      this.subAssetsGridService.getData(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.parentGroup,
        this.baseQuery
      )
    );

    promises.push(this.subAssetsGridService.getTotal(this.parentGroup, this.baseQuery));
    promises.push(
      this.subAssetsGridService.getCount(
        dataSourceModifier.columns,
        dataSourceModifier.pagination,
        this.parentGroup,
        this.baseQuery
      )
    );

    const [dataResponse, size, filteredSize] = await Promise.all(promises);
    if (!counters) {
      counters = {
        size,
        filteredSize
      };
    }
    this.onColumnsChange.emit(dataSourceModifier.columns);

    return {
      res: dataResponse.res,
      data: dataResponse.data,
      paging: dataResponse.paging,
      ...counters
    };
  }

  configChange(config) {
    this.subAssetsGridService.saveConfig(config);
  }
}
