import { Injectable } from '@angular/core';
import { IIdentified, IManagedObject, InventoryService } from '@c8y/client';
import { GroupForm } from './add-group.model';
import { GroupFragment } from '@c8y/ngx-components/assets-navigator';

@Injectable()
export class AddGroupService {
  private GROUP_FRAGMENT_TYPE = 'c8y_IsDeviceGroup';

  constructor(private inventoryService: InventoryService) {}

  async createGroupAndAssignDevices(
    groupForm: GroupForm,
    groupContextId: string | number,
    selectedDevices: string[]
  ): Promise<IManagedObject | IIdentified> {
    let group: IManagedObject | IIdentified;
    const { name, description } = groupForm;
    const newGroupMO = this.getGroupMO(name, description, groupContextId);

    if (groupContextId) {
      group = (await this.inventoryService.childAssetsCreate(newGroupMO, groupContextId)).data;
    } else {
      group = (await this.inventoryService.create(newGroupMO)).data;
    }

    if (selectedDevices.length > 0) {
      await this.assignDevices(group.id, selectedDevices);
    }

    return group;
  }

  private getGroupMO(
    name: string,
    description: string = '',
    groupContextId: string | number
  ): Partial<IManagedObject> {
    const group = {
      type: this.getGroupType(groupContextId),
      [this.GROUP_FRAGMENT_TYPE]: {},
      name,
      c8y_Notes: description
    };

    return group;
  }

  private getGroupType(
    groupContextId: string | number
  ): GroupFragment.subGroupType | GroupFragment.groupType {
    return groupContextId ? GroupFragment.subGroupType : GroupFragment.groupType;
  }

  private async assignDevices(id: string | number, selectedDevices: string[]) {
    const promises = [];

    selectedDevices.forEach(moId => {
      promises.push(this.inventoryService.childAssetsAdd(moId, id));
    });

    return await Promise.all(promises);
  }
}
