import { Component, Input, ViewChild } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import {
  ConfirmModalComponent, GainsightService, gettext, ModalLabels,
  Status,
  StatusType
} from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import { Subject } from 'rxjs';
import { DeleteModalCheckboxes } from './delete-assets-modal.model';

@Component({
  selector: 'c8y-delete-assets-modal',
  templateUrl: './delete-assets-modal.component.html'
})
export class DeleteAssetsModalComponent {
  @Input() showWithCascadeCheckbox: boolean = true;
  @Input() showWithDeviceUserCheckbox: boolean = false;
  @Input() asset: IManagedObject;
  @ViewChild('modalRef', { static: false }) modalRef: ConfirmModalComponent;
  closeSubject: Subject<DeleteModalCheckboxes> = new Subject();
  labels: ModalLabels = { ok: gettext('Delete'), cancel: gettext('Cancel') };
  title: string = gettext('Delete');
  status: StatusType = Status.DANGER;
  config: DeleteModalCheckboxes = {
    cascade: false,
    withDeviceUser: false
  };
  message: string;

  constructor(
    private translateService: TranslateService,
    private gainsightService: GainsightService
  ) {
  }

  ngOnInit() {
    this.message = this.translateService.instant(
      gettext(
        'You are about to delete asset: "{{name}}". This operation is irreversible. Do you want to proceed?'
      ),
      { name: this.asset.name }
    );
  }

  async ngAfterViewInit() {
    try {
      await this.modalRef.result;
      this.onClose();
    } catch (error) {
      this.onDismiss();
    }
  }

  onClose() {
    this.gainsightService.triggerEvent('deleteAssetsModal:AssetDeleted');
    this.closeSubject.next(this.config);
    this.closeSubject.complete();
  }

  onDismiss() {
    this.gainsightService.triggerEvent('deleteAssetsModal:Cancel');
    this.closeSubject.complete();
  }
}
